*,
*::before,
*::after {
	box-sizing: border-box;
}

body {
	background-color: #3b4664;
	margin: 0;
}

.calc-body {
	background-color: #3b4664;
	width: 600px;
	height: auto;
	margin: 0 auto;
	overflow: hidden;
	margin-top: 30px;
	border-radius: 20px;
}

.header {
	color: white;
	background-color: #3b4664;
	width: 600px;
	height: 16px;
	font-weight: bold;
	font-size: 20px;
	letter-spacing: 2px;
	padding: 20px 0px 20px 0px;
	display: flex;
	align-items: center;
	justify-content: left;
	margin: 20px 0px 10px 20px;
}

.screen {
	width: 600px;
	height: 40px;
	padding: 50px;
	color: white;
	font-size: 40px;
	font-weight: bolder;
	background-color: #181f32;
	border-radius: 12px;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: space-around;
}

.prev-op {
	font-size: 30px;
	font-weight: lighter;
}

.keypad {
	display: grid;
	gap: 1em;
	grid-template-columns: auto auto auto auto;
	margin-top: 20px;
	padding: 20px;
	background-color: #242e41;
	border-radius: 12px;
}

.keypad > button {
	box-sizing: content-box;
	display: inline-block;
	color: #4d4f57;
	border: none;
	border-radius: 15px;
	padding: 20px;
	font-size: 2rem;
	background-color: #e9e2db;
	display: flex;
	justify-content: center;
	align-items: center;
	font-weight: bolder;
	text-align: center;
	cursor: pointer;
}
.keypad > button:hover,
.keypad > button:focus {
	color: seagreen;
}

.keypad > .del {
	background-color: #647299;
	color: #e7eef3;
	font-weight: bolder;
	text-align: center;
	border-radius: 15px;
	border: none;
	padding: 10px;
	font-size: 2rem;
}
.keypad > .del:hover,
.keypad > .del:focus {
	color: red;
	border: solid 2px white;
}

.keypad > .reset {
	grid-column: span 2;
	background-color: #647299;
	color: #e7eef3;
	border: none;
	font-weight: bolder;
	text-align: center;
	border-radius: 15px;
	border: none;
	padding: 10px;
	font-size: 2rem;
}
.keypad > .reset:hover {
	color: red;
	border: solid 2px white;
}

.keypad > .equal {
	grid-column: span 2;
	color: #ffdbd4;
	background-color: #d1402e;
	font-weight: bolder;
	text-align: center;
	border-radius: 15px;
	border: none;
	padding: 10px;
	font-size: 2rem;
}
.keypad > .equal:hover,
.keypad > .equal:focus {
	color: green;
	border: solid 2px white;
}
